<template>
<transition name="slide-top-menu">
  <div class="slidemenu" v-show="visible">
    <div class="slidemenu-conent">
     <!--  <div class="login-header">
        <div class="top-back" @click="goBack">
          <span class="iconfont icon-return-arrow"></span>
        </div>
        <div class="center">
          注册
        </div>
      </div> -->
       <custHeader :title="title"></custHeader>
      <div class="login-img"></div>
      <div class="login-inputs">
        <!-- <mt-field label="证件类型" placeholder="请选择证件类型" v-model="acctType" v-lfoucs="selectTypeVisibleShow" v-remoteoptions="dicts.acctType" disabled></mt-field> -->

        <mt-field label="手机号码" placeholder="请输入手机号码" type="tel" v-validtype="rules.mb" :state="rules.mb.state" v-model="mb"></mt-field>
        <mt-field label="登录密码" placeholder="请输入登录密码" type="password" v-validtype="rules.password" :state="rules.password.state" v-model="trdPassword"></mt-field>

        <mt-field label="验证码" placeholder="请输入验证码" v-model="captcha"><mt-button size="small" type="primary" :disabled=idDisabled @click="send">{{ capMessage }}</mt-button></mt-field>


        <mt-button type="danger" size="large" class="reg" @click="reg">注册</mt-button>



      </div>
      <mt-popup
            v-model="selectTypeVisible"
            style="width:80%"
            pop-transtion="popop-fade"
            class="select-dict"
          >
            <mt-picker :slots="selectTypeSlots"  valueKey="name" @change="selectTypeChange" :visible-item-count="3"></mt-picker>
            <div class="select-dict-btns"><button class="btn" @click="dissSelectType">取消</button>
              <button class="btn" @click="sureSelectType">确认</button>
              </div>
          </mt-popup>
    </div>
  </div>
</transition>
</template>

<script>
import {
  Field,
  Button,
  MessageBox,
  Picker,
  Popup
} from 'mint-ui';
import cookieUtil from 'js-cookie'
import custHeader from  'components/custHeader'
Vue.component(Button.name, Button);
Vue.component(Popup.name, Popup);
Vue.component(Picker.name, Picker);
Vue.component(Field.name, Field);
export default {
  name: 'slidemenu',
  data() {
    return {
       title:"注册",
       dicts:{
        acctType:{
          name:'ACCT_TYPE',
          dataKey:"selectTypeSlots"
        }
       } ,
      visible: true,
      idDisabled:false,
      trdPassword:'',
      mb:'',
      captcha:'',
      capMessage: '发送短信',

      acctType:'',
      acctTypeValue:'',
      selectTypeVisible:false,
       selectTypeSlots:[{
        flex:1,
        values:[],
        className:"slots"
      }],
      rules:{
        mb:{
          str:"required,mobile",
          state:''
        },
        password:{
          str:"required",
          state:''
        }
      }

    }
  },
  computed: {

  },
  created: function() {

  },
  components:{
    custHeader
  },
  watch: {
    value(val) {
      this.visible = val.isShow;
    },
    visible(val) {
      this.value.isShow = true;
    }
  },
  methods: {
    goBack: function() {
      this.$router.go(-2);
    },
    selectTypeChange:function(picker,value){
      console.log(value);
      if(value[0]){
        this.acctType=value[0].name;
        this.acctTypeValue=value[0].value;
      }
    },
    selectTypeVisibleShow:function(){
      this.selectTypeVisible=true;
    },
    sureSelectType:function(){
      this.selectTypeVisible = false
    },
    dissSelectType:function(){
      this.selectTypeVisible = false
    },


    send:function(){
      var that = this;
      if(!(this.rules.mb.state !== 'error' &&  this.rules.mb.state !== '' && this.mb)){
        MessageBox("提示", "你的手机号格式不正确");
        return false
      }
      axiosUtil.sendSms({
        askBizCode: 100220,
        mb:this.mb
      }).then(function(data){
        if (data.errorcode == 1) {
          MessageBox("提示", data.errormsg);
        } else{
          let time = 60;
          that.capMessage = time + "s后重新发送"
          that.idDisabled = true;
          let timer = setInterval(function(){
            time = time - 1;
            that.capMessage = time + "s后重新发送";
            if(!time){
              // time = 60
              that.capMessage = '发送短信'
              that.idDisabled = false;
              clearInterval(timer)
            }
          },1000)
        }
      })

    },

    reg: function() {
      var self = this;
      if(!(this.mb && this.captcha && this.trdPassword)){
        MessageBox("提示", "信息填写不完整");
        return false
      }
      if(global.isOpenMd5){
        this.trdPassword=global.md5(this.trdPassword);
      }
      axiosUtil.register({
        acctType:0,//机构类型默认个人 this.acctTypeValue
        smsAuthCode: this.captcha,
        mobilePhone:this.mb,
        loginPassword: this.trdPassword
      }).then(function(data) {
        var data = data.data;
        if (data.errorcode == 1) {
          MessageBox("提示", data.errormsg);
        } else {
          var userData = data.errormsg;
          MessageBox.alert("客户代码是"+data.errormsg+",请用此登录!").then(action =>{
            self.$router.go(-1);
          })
          // axiosUtil.upCltStkTrdacctQry().then(function(data) {
          //   var data = data.data;
          //   var trdacct = data[0].trdacct; //0000160115
          //   cookieUtil.set("trdAcct", data[0].trdacct,{expires:1,path:'/shop-web'})
          //   cookieUtil.set("userCode", userData.data.userCode,{expires:1,path:'/shop-web'})
          //   self.$router.go(-2);
          // })

        }

      })
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.slidemenu {
  /*	height: 100%;
	width: 100%;
	position: fixed;
	top: 0;
  left:0;
	background-color: rgba(0,0,0,.3);
	z-index: 998;
  transition: 3s all ease;*/
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, .3);
  transition: .5s all ease;
}

.slidemenu-conent {
  height: 100%;
  width: 100%;
  background: #fff;
}

.menu-list {
  color: #111;
}

.login-header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 3;
  height: 45px;
  line-height: 45px;
  font-size: 19px;
  background: #ef4f4f;
  color: #ffffff;
  text-align: center;
}

.top-back {
  max-width: 85px;
  float: left;
}
.login-img {
  height: 100px;
}

.inputDiv{
  height: 48px;
  line-height: 47px;
  font-size: 17px;
  border-top: 1px solid #ccc;
  margin-left: 10px;
}

.inputLabel{
  display: inline-block;
  width: 104px;
  float: left;
  /*padding-left: 13px;*/
}
.inputInput{
  display: inline-block;
  float: left;
  border: 0;
  outline: 0;
  height: 27px;
  font-size: 17px;
  margin-top: 8px;
}

.btn{
  width: 49%;
  background-color: #fff;
  border: 0;
  height: 40px;
  font-size: 20px;
  outline: 0;
}

.reg{
  position: fixed;
  bottom: 3px;
  right: -1px;
}




</style>



